import styled from "styled-components";

export const Body = styled.body`
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const Container = styled.div`
  top: 25%;
  position: absolute;
  transform: transform(-50%, -50%);
  margin: 0 auto;
  width: 256px;
  text-align: center;
  border: 10px solid #0b0f1f;
  border-radius: 5%;
  background: #0b0f1f;
`;

export const Caixa = styled.input`
  height: 73px;
  width: 230px;
  background-color: #0b0f1f;
  border: 1px solid #262834;
  border-radius: 5px;
  color: #ffffff;
  text-align: right;
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 2px;
`;

export const Teclado = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
`;

export const Limpar = styled.button`
  grid-column: 1/3;
  margin: 5px;
  border: none;
  font-size: 18px;
  font-weight: 700;
  border-radius: 5px;
  background: #56cbdb;
  cursor: pointer;
`;

export const Resultado = styled.button`
  grid-column: 3/5;
  margin: 5px;
  border: none;
  font-size: 20px;
  font-weight: 700;
  border-radius: 5px;
  background: #56cbdb;
  cursor: pointer;
`;

export const Botao = styled.button`
  margin: 3px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: #262834;
  color: #ffff;
  font-size: 20px;
  font-weight: 700;
`;