import React, { useState } from "react";
import { Body, Container, Teclado, Caixa, Limpar, Resultado, Botao } from "./Styles";

const App = () => {
  const [result, setResult] = useState("");

  const handleClick = (event) => {
    setResult(result.concat(event.target.name));
  };

  //função para apagar todos os caracteres!
  const clear = () => {
    setResult("");
  };

  //função para apagar um caracter por vez!
  const backspace = () => {
    setResult(result.slice(0, -1));
  };

  //função para mostrar o resultado da operação e mensagem de erro!
  const calcular = () => {
    try {
      setResult(eval(result).toString());
    } catch (err) {
      setResult("Invalido!");
    }
  };

  return (
    <Body>
      <Container>
        <form>
          <Caixa type="text" value={result} />
        </form>

        <Teclado>
          <Limpar onClick={clear} id="clear">
            Limpar
          </Limpar>
          <Botao onClick={backspace} id="backspace">
            C
          </Botao>
          <Botao name="-" onClick={handleClick}>
            &ndash;
          </Botao>
          <Botao name="7" onClick={handleClick}>
            7
          </Botao>
          <Botao name="8" onClick={handleClick}>
            8
          </Botao>
          <Botao name="9" onClick={handleClick}>
            9
          </Botao>
          <Botao name="/" onClick={handleClick}>
            &divide;
          </Botao>
          <Botao name="4" onClick={handleClick}>
            4
          </Botao>
          <Botao name="5" onClick={handleClick}>
            5
          </Botao>
          <Botao name="6" onClick={handleClick}>
            6
          </Botao>
          <Botao name="*" onClick={handleClick}>
            &times;
          </Botao>
          <Botao name="1" onClick={handleClick}>
            1
          </Botao>
          <Botao name="2" onClick={handleClick}>
            2
          </Botao>
          <Botao name="3" onClick={handleClick}>
            3
          </Botao>
          <Botao name="+" onClick={handleClick}>
            +
          </Botao>
          <Botao name="0" onClick={handleClick}>
            0
          </Botao>
          <Botao name="," onClick={handleClick}>
            ,
          </Botao>
          <Resultado onClick={calcular} id="result">
            =
          </Resultado>
        </Teclado>
      </Container>
    </Body>
  );
};

export default App;